import { MODULE_ID } from '@utils/module'

type OwnModule = 'anonymous' | 'idleuh' | 'pf2e-hero-actions'

type ReturnedModule<T extends Record<string, any> | unknown = unknown> = Module<T> | undefined
type ReturnedModuleApi<T extends Record<string, any>> = Module<T>['api'] | undefined

export function getModule<T extends Record<string, any> | unknown = unknown>(id: string | OwnModule) {
    return game.modules.get(id) as ReturnedModule<T>
}

export function getCurrentModule<T extends Record<string, any> | unknown = unknown>() {
    return getModule(MODULE_ID) as Module<T>
}

export function getModuleApi<T extends Record<string, any>>(id: string | OwnModule) {
    return getModule<T>(id)?.api as ReturnedModuleApi<T>
}
